//
// CUSTOM RADIO / CHECKBOX
// --------------------------------------------------------------------------

// https://codepen.io/triss90/pen/XNEdRe

$color1: #f4f4f4;
$color2: #3197EE;


.radio {

    input[type="radio"] {
        position: absolute;
        opacity: 0;

        + .radio-label {
            &:before {
                content: '';
                background: $color1;
                border-radius: 100%;
                border: 1px solid darken($color1, 25%);
                display: inline-block;
                width: 1.4em;
                height: 1.4em;
                position: relative;
                top: -0.2em;
                margin-right: 1em;
                vertical-align: top;
                cursor: pointer;
                text-align: center;
                transition: all 250ms ease;
            }
        }

        &:checked {
            + .radio-label {
                &:before {
                    background-color: $color2;
                    box-shadow: inset 0 0 0 4px $color1;
                }
            }
        }

        &:focus {
            + .radio-label {
                &:before {
                    outline: none;
                    border-color: $color2;
                }
            }
        }

        &:disabled {
            + .radio-label {
                &:before {
                    box-shadow: inset 0 0 0 4px $color1;
                    border-color: darken($color1, 25%);
                    background: darken($color1, 25%);
                }
            }
        }

        + .radio-label {
            &:empty {
                &:before {
                    margin-right: 0;
                }

            }
        }
    }
}

.checkbox {

    input[type="checkbox"] {
        position: absolute;
        opacity: 0;

        + .checkbox-label {
            span {
                display: inline-block;
                margin-top: .6em;
                font-size: 90%;
                line-height: 1;
            }

            &:before {
                content: '';
                display: inline-block;
                width: 1.8em;
                height: 1.8em;
                position: relative;
                top: 2px;
                margin-right: .6em;
                vertical-align: top;
                text-align: center;

                // background: $color1;
                // border-radius: 0;
                // border: 1px solid darken($color1, 25%);

                border: 1px solid rgba(0,0,0,.5);
                border-radius: 2px;
                background-color: $white;
                background-position: 3px 3px;

                cursor: pointer;
                transition: all 250ms ease;
            }
        }

        &:checked {
            + .checkbox-label {
                &:before {
                    background-color: $color2;
                    box-shadow: inset 0 0 0 0 $color1;
                    background-image: url("data:image/svg+xml,%3Csvg width='32' height='32' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E");
                }
            }
        }

        &:focus {
            + .checkbox-label {
                &:before {
                    outline: none;
                    border-color: $color2;
                }
            }
        }

        &:disabled {
            + .checkbox-label {
                &:before {
                    box-shadow: inset 0 0 0 4px $color1;
                    border-color: darken($color1, 25%);
                    background: darken($color1, 25%);
                }
            }
        }

        + .checkbox-label {
            &:empty {
                &:before {
                    margin-right: 0;
                }
            }
        }
    }
}



