            color: #494949;
            background-color: #d8d8d8;
            border-radius: 50%;
            box-shadow: 0 0 0 0 $white;
            transition: all .2s ease-in-out;

            &:hover, &:focus, &:active {
                @include button-hover;
                outline-width: 0;
                box-shadow: 0 0 0 3px $white;

                &:after {
                    color: $white;
                }
            }

            &:after {
                position: absolute;
                top: 10px;
                left: 18px;
                content: '\e0b7';
                font-family: 'Material Icons';
                font-size: 36px;
            }
        }
    }

    img {
        margin-bottom: .8em;
    }
    p {
        font-size: 80%;
        color: #ccc;
    }

    .footer-info {
        padding: 1.6em 0;
    }

    .footer-links {
        padding: .8em 0;
        border-top: 1px solid #808080;

        p {
            margin-bottom: 0;
        }
    }

    .legal-info {
        @include reset-list;

        li {
            position: relative;
            margin-right: .4em;
            padding-right: .6em;

            &:after {
                position: absolute;
                top: 8px;
                right: 0;
                width: 1px;
                height: 15px;
                content: "";
                background: $white;
            }

            &:last-child {
                &:after {
                    display: none;
                }
            }
        }

        a {
            font-size: 90%;
            font-weight: 600;
            color: $white;

            &:hover, &:focus, &:active {
                @include link-hover;
            }
        }
    }

    .social-media {
        @include reset-list;

        li {
            margin-left: .6em;
        }

        a {
            display: block;
            width: 38px;
            height: 38px;
            text-align: center;
            color: $white;
            border-radius: 50%;
            transition: all .2s ease-in-out;

            &:hover, &:focus, &:active {
                @include button-hover;
            }

            i {
                line-height: 2;
            }
        }
    }

}


//
// BUTTONS
// --------------------------------------------------------------------------

.btn-solid {
    @include reset-button;
    color: $white;
    background-color: $medium-black;
    border-color: $medium-black;
}

.btn-main {
    @include reset-button;

    &:hover, &:focus, &:active {
        @include button-hover-shadow;
    }
}

.btn-border {
    @include reset-button;
    color: $black;
    background-color: $white;
    border: 1px solid $black;
    border-radius: .25rem;

    &.white {
        color: $white;
        background-color: transparent;
        border-color: $white;
    }

    &:hover, &:focus, &:active {
        @include button-hover;
        // @include button-hover-shadow;
    }

    &.view-more {
        min-width: 33.3333%;
    }
}

.btn-arrow {
    @include button-arrow;

    &:hover, &:focus, &:active {
        @include button-hover;
    }
}

.btn-border-arrow {
    @include reset-button;
    @include button-arrow
    color: $complementary-color;
    background-color: $white;
    border: 2px solid $complementary-color;
    border-radius: .25rem;

    &:hover, &:focus, &:active {
        @include button-hover;
        // @include button-hover-shadow;
    }
}

.btn-naked {
    @include reset-button;
    color: $black;
    background-color: $white;

    &:hover, &:focus, &:active {
        @include button-hover;
    }

    &.view-more {
        min-width: 33.3333%;
    }
}

.button-wrapper {
    text-align: center;
}


.button-bookmark {
    @include reset-button;
    position: absolute;
    top: 0;
    right: 0;
    width: 38px;
    height: 38px;
    margin: 0 0;
    padding: .2em;
    color: $complementary-color;
    background-color: transparent;
    border-color: transparent;
    border-radius: 50%;
    z-index: 3;

    &:after {
        position: relative;
        top: 0;
        left: 0;
        content: '\e867';
        font-family: 'Material Icons';
        font-size: 24px;
        font-weight: normal;
        line-height: 1.2;
    }

    &:hover, &:focus, &:active {
        // @include button-hover;
        color: $white;
        background-color: $complementary-color;
        border-color: $complementary-color;
    }

    &.bookmark-on {
        &:after {
            content: '\e866';
        }
    }
}



//
// HOME
// --------------------------------------------------------------------------

#home-header {
    $header-height: 84vh;
    $header-top: 114px;

    width: 100%;
    height: $header-height;
    margin-bottom: 5em;

    .full-width {
        display: block;
        width: 100vw;
        height: 100%;
        position: relative;
        left: 50%;
        right: 50%;
        margin: 0 -50vw;
        padding: 0 0;
        overflow: hidden;

        img {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            max-width: none;
            height: auto;
        }
    }

    .header-wrapper {
        @include reset-flex;

        position: absolute;
        top: $header-top;
        width: 30vw;
        // height: calc(#{$header-height} - #{$header-top});
        margin: 0 0;
        padding: 0 0;
        background-color: $white;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: -200%;
            width: 200%;
            height: 100%;
            background-color: $white;
        }

        .category {
            margin-bottom: .6em;
            font-size: 90%;
            font-weight: 600;
            text-transform: uppercase;
        }

        .header-info {
            padding: 3em 3em 0 0;
            background-color: $white;

            .category {
                color: $gray-medium;
            }

            h1 {
                margin-bottom: 1em;
                font-weight: 700;
                letter-spacing: -.03em;
            }
        }

        .header-button {
            $transition-hover: all .3s ease-out;

            // position: relative;
            position: absolute;
            bottom: -8px;

            padding: 2em 4em 2.6em 0;
            background-color: $complementary-color;

            &:before {
                content: "";
                position: absolute;
                top: 0;
                left: -200%;
                width: 200%;
                height: 100%;
                background-color: $complementary-color;
            }

            &:after {
                content: "";
                position: absolute;
                top: 0;
                right: -35%;
                width: 35%;
                height: 100%;
                background-color: $main-color;
                transition: $transition-hover;
            }

            .category {
                color: $main-color;
            }

            .intro {
                font-weight: 600;
                line-height: 1.2;
                color: $white;
            }

            .icon {
                position: absolute;
                top: calc(50% - 32px);
                right: -20%;
                width: auto;
                height: auto;
                font-size: 64px;
                line-height: 1;
                color: $complementary-color;
                z-index: 3;
                transition: $transition-hover;
            }

            &:hover,&:focus,&:active {

                .icon {
                    right: -40%;
                    color: $main-color;
                }

                &:after {
                    right: -50%;
                    width: 50%;
                    background-color: $complementary-color;
                }
            }
        }
    }
}


