// ==========================================================================
// GENERAL COMPONENTS
// ==========================================================================


//
// SHARED
// --------------------------------------------------------------------------

.hash-text {
    position: relative;
    display: block;
    margin: 0 0 1em;
    font-weight: 600;
    line-height: 1;
    color: $dark-black;

    &:before {
        content: "#";
    }

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: -1em;
        width: 4px;
        height: 50px;
        background-color: $black;
    }
}

.breadcrumb {
    padding: 0 0;
    margin: 1em 0 2.6rem;
    list-style: none;
    background-color: $white;
    border-radius: 0;

    .breadcrumb-item {
        font-size: 80%;

        a {
            @include text-link($black,$secondary-color,$white,$secondary-color);
        }
    }
}

.action-icons {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    @include reset-list;
    margin-top: 2em;

    li {
        margin-left: .6em;

        a {
            display: block;
            width: 38px;
            height: 38px;
            text-align: center;
            color: $black;
            border: 1px solid #c6c6c6;
            border-radius: 50%;
            transition: all .2s ease-in-out;

            i {
                font-size: 18px;
                line-height: 2;
            }

            &:hover, &:focus, &:active {
                @include button-hover;

                i {
                    color: $white;
                }
            }
        }
    }
}

.back-button {
    position: relative;
    padding-left: 1em;
    font-size: 80%;
    font-weight: 600;
    color: $black;

     &:after {
        @include icons-ionicons(2px,auto,auto,0);
        top: 2px;
        left: 0;
        content: '\f3cf';
        font-size: 18px;
        font-weight: normal;
    }

    &:hover, &:focus, &:active {
        @include link-hover;
    }
}

.bg-colored {
    padding-top: 3em;
    background-color: $light-black;
}


//
// MAIN PHOTO HEADER
// --------------------------------------------------------------------------

#page-header {
    margin-bottom: 3em;

    .full-width {
        display: block;
        width: 100vw;
        height: 350px;
        position: relative;
        left: 50%;
        right: 50%;
        margin: 0 -50vw;
        padding: 0 0;
        overflow: hidden;

        img {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            max-width: none;
            height: auto;

            &.v-top {
                top: 0;
            }

            &.v-center {
                top: -120%;
            }

            &.v-middle {
                top: -50%;
            }

            &.vt20 {
                top: -20%;
            }

            &.v-bottom {
                top: auto;
                bottom: 0;
            }

            &.vb125 {
                top: auto;
                bottom: -125%;
            }

            &.vb250 {
                top: auto;
                bottom: -250%;
            }

            &.x-flip {
                transform: scaleX(-1);
            }
        }
    }

    .header-info {
        position: absolute;
        bottom: 0;
        width: 100%;
        // height: 160px;
        margin: 0 0;
        padding: 0 0;
        background-color: $white;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background-color: $white;
        }

        .hash-text {
            margin-top: 1.4em;
        }

        h1 {
            padding: 0 2em .2em 0;
            text-transform: uppercase;
        }

        &.event {
            h1 {
                text-transform: none;
                letter-spacing: 0;
            }
        }

        .section {
            font-size: 80%;
            font-weight: 600;
            text-transform: uppercase;
            color: $medium-black;
        }

        .date-time,
        .address {
            font-size: 90%;

            &:before {
                opacity: .5;
            }
        }

        .date-time {
            margin: .4em 0;
        }

        .address {
            margin-bottom: 1.6em;

            .location {
                padding-left: 10px;

                &:before {
                    position: relative;
                    left: -5px;
                    content: '·';

                }
            }
        }

        .breadcrumb {
            margin: .5em 0 0;
        }

        .action-icons,
        .back-button {
            position: absolute;
            right: 2em;
        }

        .action-icons {
            bottom: 8px;
        }

        .back-button {
            top: .8em;
        }
    }
}

.cover-photo {
    position: relative;
    background-color: $white;
    background-attachment: fixed;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh
}


//
// ICONS
// --------------------------------------------------------------------------

.date,
.time,
.date-time,
.address,
.contact {
    position: relative;
    padding-left: 1.6em;

    &:before {
        @include icons-ionicons(0,auto,auto,0);
        // opacity: .5;
    }
}

.date {
    font-variant: small-caps;

    strong {
        font-size: 110%;
    }

    &:before {
        content: '\f2d1';
    }
}

.time {
    &:before {
        content: '\f402';
    }
}

.date-time {
    &:before {
        content: '\f2d1';
    }
}

.address {
    .city {
        text-transform: uppercase;
    }

    .location {
        font-size: 90%;
        opacity: .5;
    }

    &:before {
        content: '\f3a3';
    }
}

.contact {
    .name {
        font-weight: 600;
    }

    .tel {
        position: relative;
        padding-left: 10px;

        &:before {
            position: relative;
            left: -5px;
            content: "|";
        }

    }

    &:before {
        content: '\f3a0';
    }
}



//
// BLOG LIKE LAYOUT
// --------------------------------------------------------------------------

.article-section {
    margin-bottom: 4em;
    padding-right: 3em;

    h2 {
        position: relative;
        margin-bottom: 1.8em;
        margin-left: -1em;
        font-size: 3rem;
        font-weight: 700;

        &:not(:first-child) {
            margin-top: 2em;
        }

        &:after {
            content: "";
            position: absolute;
            top: 1.8em;
            left: 0;
            width: 60px;
            height: 4px;
            background-color: $medium-black;
        }
    }

    a {
        @include text-link($secondary-color,$black,$white,$black);
    }

    .intro {
        font-size: 110%;
        font-weight: 700;
    }

    .cite-quote {
        position: relative;
        width: 70%;
        margin: 2em auto;
        padding: 0 0 0 1.6em;
        text-align: left;

        p {
            margin-bottom: .5em;
            font-size: 2.8rem;
            font-weight: 600;
            line-height: 1.2;
            color: $dark-black;
        }

        cite {
            font-size: 2rem;
            font-weight: 600;
            font-style: normal;
            color: $medium-black;
        }

        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 6px;
            height: 40px;
            background-color: $black;
        }

    }

    .inline-quote {
        // quotes: "“" "”" "‘" "’";
        position: relative;
        max-width: 65%;
        margin: 3em auto;
        font-size: 110%;
        font-weight: 600;
        text-align: center;

        &:before,
        &:after {
            content: '\0022';
            position: absolute;
            font-size: 12rem;
            font-weight: 700;
            font-style: italic;
            color: $light-black;
        }

        &:before {
            // content: '\201C';
            // content: open-quote;
            top: -85px;
            left: -45px;
            transform: rotateY(180deg);
        }

        &:after {
            // content: '\201D';
            // content: close-quote;
            bottom: -85px;
            right: -45px;
        }

        p {
            margin-bottom: 0;
        }
    }
}

.article-aside {

    section {
        margin-bottom: 3em;

        h3 {
            margin-bottom: 1.3em;
