    }

    li {
        margin: .2em 0;
    }

    a {
        position: relative;
        display: block;
        padding: .2em 1.4em;
        font-size: 90%;
        font-weight: 600;
        color: $black;
        border-radius: 20px;
        transition: all .3s ease-in-out;

        &:after {
            @include icons-ionicons(4px,20px,auto,auto);
