//
// RESPOSIVE TABLE
// --------------------------------------------------------------------------



//
// LISTS
// --------------------------------------------------------------------------

.bullet-list {
    @include reset-list;
    display: block;

    li {
        position: relative;
        margin-left: 1em;
        padding-left: 1em;
        color: $gray-800;

        &:before {
