            display: block;
            line-height: 1;
            color: $gray-600;
        }

        // &:last-child {
        //     margin-bottom: 0;
        //     padding-bottom: 0;
        // }
    }
}

.list-button-link {
    @include reset-list;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 -1em;

    li {
        display: flex;
        flex-direction: column;
        flex: 0 0 calc(33.3333% - 2em);
        margin: 0 1em;

        a {
            flex: 1 0 auto;
            position: relative;
            display: block;
            padding: 1.2em 2em .6em .8em;
            font-weight: 700;
            line-height: 1;
            color: $black;
            border-bottom: 2px solid $medium-black;

            &:after {
                position: absolute;
                top: 16px;
                right: 12px;
                content: '\f3d3';
                font-family: 'Ionicons';
                font-size: 32px;
                font-weight: 600;
            }

            &:hover, &:focus, &:active {
                @include button-hover-shadow;
                box-shadow: -5px 5px 1px $primary-color, 0 -2px 1px $secondary-color;
            }


        }


