}


//
// CONTACT BOX
// --------------------------------------------------------------------------


//
// TABS
// --------------------------------------------------------------------------

.nav-tabs {
    border-bottom: none;
}

.nav-tabs .nav-item {
    margin-bottom: -1px
}

.nav-tabs .nav-link {
    padding: .6em 2.4em;
    font-size: 1.4em;
    font-weight: 600;
    letter-spacing: -.02em;
    color: $gray-medium;
    border: 1px solid transparent;
    border-top-left-radius: .25rem;
    border-top-right-radius: .25rem
}

.nav-tabs .nav-link:focus,.nav-tabs .nav-link:hover {
    color: $black;
    border-color: $white-dark $white-dark $white-dark;
}

.nav-tabs .nav-item.show .nav-link,.nav-tabs .nav-link.active {
    color: $black;
    background-color: $white-medium;
    border-color: $white-dark $white-dark $white
}

.nav-tabs .nav-link.disabled {
    color: $white-medium;
    background-color: transparent;
    border-color: transparent
}





//
// ACCORDION
// --------------------------------------------------------------------------

.accordion {
    margin: 3em 0 2em;

    &.panel-group {

        .panel,
        .panel-heading {
            @include reset-margins;
        }

        .panel-title {
            font-size: 2rem;
            letter-spacing: -.0315625em;

            a {
                position: relative;
                display: block;
                padding: 1em 1.8em;
                color: $black;
                background-color: $white;
                border-bottom: 1px solid #afafaf;
                transition: all .3s ease-in-out;

                &:hover, &:focus, &:active {
                    text-decoration: none;
                    background-color: #f7f7f7;
                }

                &:before {
                    @include icons-ionicons(20px,30px,auto,auto);
                    content: '\f3d1';
                }

                &:not(.collapsed) {
                    font-weight: 700;
                    background-color: #f7f7f7;
                    border-bottom: 1px solid transparent;

                    &:before {
                        transform: rotate(90deg);
                    }
                }
            }
        }

        .panel-body {
            position: relative;
            padding: 0 2em 2em;
            background-color: #f7f7f7;

            &:after {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 1px;
                background-color: $black;
                content: "";
            }

            h3 {
                padding: .5em 0 1em;
                font-size: 1.6rem;
                font-weight: 700;
                text-transform: uppercase;
                color: $black;
            }

            p {
                font-size: 90%;
                color: $gray-800;

                &:last-child {
                    margin-bottom: 0
                }
            }

            ul {
                margin: .5em 0 2em;
                font-size: 90%;
                color: $gray-800;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}






@mixin reset-margins {
    margin: 0;
    padding: 0;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin reset-flex {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    align-items: center;
    align-content: space-between;
}

@mixin reset-flex-horizontal {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
}

@mixin reset-button {
    padding: .8em 1.4em;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.2;
    letter-spacing: 0;
    // text-transform: uppercase;
    background: none;
    border: none;
    border-radius: 0;
    transition: all .3s ease-in-out;
}

@mixin button-arrow {
    position: relative;
    width: 50%;
    padding-right: 5em;
    text-align: left;

    &:after {
        position: absolute;
        top: 3px;
        right: 12px;
        content: '\f3d3';
        font-family: 'Ionicons';
        font-size: 32px;
        font-weight: normal;
    }
}

@mixin button-hover {
    color: $white;
    background-color: $complementary-color;
    border-color: $complementary-color;
}

@mixin button-hover-shadow {
    color: $white;
    background-color: $complementary-color;
    border-color: $complementary-color;
    box-shadow: -4px 4px 1px $primary-color;
}

@mixin text-link($color,$hover,$shadow-color,$shadow-hover) {
    color: $color;
    font-weight: 600;
    box-shadow: 0 1px 0 $shadow-color;
    transition: all .2s linear;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $hover;
        box-shadow: 0 1px 0 $shadow-hover;
    }
}

@mixin link-hover {
    text-decoration: none;
    color: $complementary-color;
    // box-shadow: 0 1px 0 $complementary-color;
}

@mixin link-underline-hover {
    color: $black;
    box-shadow: 0 1px 0 $black;
}

@mixin icons-ionicons($top,$right,$bottom,$left) {
    position: absolute;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    font-family: 'Ionicons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    font-feature-settings: 'liga';
    transition: transform .3s ease-in-out;
}

@mixin reset-figure {
    position: relative;
    margin: 0 0;
    overflow: hidden;
}

@mixin stretch-to-left {
    @include reset-figure;
    margin-left: -22vw;

    img {
        width: 100%;
        max-width: 100%;
        height: auto;
    }
}

@mixin stretch-to-right {
    @include reset-figure;
    margin-right: -22vw;

    img {
        width: 100%;
        max-width: 100%;
        height: auto;
    }
}

@mixin marked-image {
    position: relative;

    &:after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 4px;
        height: 50px;
        background-color: $black;
    }
}

@mixin marked-box {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: 40px;
        background-color: $black;
    }
}

@mixin flex-boxes {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-content: stretch;
    align-items: stretch;
    margin: 0 -1em;
}

@mixin card-boxes {
    position: relative;
    width: calc(50% - 2em);
    margin: 1em;
    text-align: left;
    color: inherit;
    border-radius: 0;
    box-shadow: 0 0 0 rgba(0,0,0,0);
    transition: .3s;

    &:hover,&:focus,&:active {
        box-shadow: 0 15px 30px rgba(0,0,0,.2);
    }

    &:after {
        content: "";
        width: 100%;
        height: 100%;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        outline: transparent solid 1px;
        -webkit-box-shadow: 0 0 0 rgba(0,0,0,.25);
        box-shadow: 0 0 0 rgba(0,0,0,.15);
        transition: .3s;
    }

    &:hover:after {
        -webkit-box-shadow: 0 15px 30px rgba(0,0,0,.17);
        box-shadow: 0 15px 30px rgba(0,0,0,.17)
    }
}

@mixin box-hover {
    color: $white;
    background-color: $complementary-color;
    transition: .2s ease-in-out;
}

@mixin box-hover-bw {
    color: $white;
    background-color: $black;
    transition: .2s ease-in-out;
}

@mixin box-hover-shadow {
    background-color: $light-black;
    box-shadow: 0 0 0 rgba(0,0,0,0);
    transition: .3s;

    &:hover,&:focus,&:active {
        box-shadow: 0 15px 30px rgba(0,0,0,.2);
    }
}

@mixin box-hover-outline {
    outline: 4px solid transparent;
    // outline-offset: 4px;
    transition: .3s;

    &:hover,&:focus,&:active {
        outline: 4px solid $complementary-color;
    }
}

@mixin highlight-text {
    margin-bottom: 0;
    font-size: 4.5rem;
    font-weight: 300;
    line-height: 1.2;
}

@mixin smaller-text {
    font-size: 90%;
    font-weight: 300;
    line-height: 1.2;
}

@mixin img-hover-fx {
    position: relative;
    overflow: hidden;
    transition: .3s;

    img {
        width: 100%;
        transform-origin: left bottom 0;
        transition: .6s;
    }

    &:hover img {
        transform: scale(1.1) translate(-20px);
    }
}


@mixin keyframes($name) {
    @keyframes #{$name} {
        @content
    }
}

@mixin animation($value) {
    animation: $value;
}

