//
// INSIGHTS
// --------------------------------------------------------------------------
.insights {
    margin-top: 4rem;
    padding: 3em 0;
    background-color: #fcfcfc; /*efefef*//*fcfcfc*/

    .choose-interests {
        margin-top: .5em;
