}


//
// HIGHLIGHT CONTENT (Articles & Events)
// --------------------------------------------------------------------------

.highlight-content-wrapper {
    text-align: center;
    margin-bottom: 4em;

    .section-header {
        margin-bottom: 3rem;
    }

    .highlight-content-boxes {
        @include flex-boxes;
        justify-content: flex-start;
    }

    .card {
        @include card-boxes;
        // padding-bottom: 1em;
        // width: calc(33.3333% - 2em);
        width: auto;
        flex: 1;
        border: none;
        border-bottom: 1px solid $gray-100;

        @include img-hover-fx;

        figure {
            @include reset-figure;
            max-height: 230px;
        }
    }

    .card-article {}

    .card-event {

        .btn {
            // width: calc(100% - 2em);
            // margin-left: 1em;
            width: 100%;
            padding-right: 3em;
            text-align: right;
            z-index: 3;

            &:hover {
                box-shadow: 0 0 0 transparent;
            }
        }
    }

    .card-header,
    .card-footer {
        background-color: transparent;
    }

    .card-header {
        position: relative;
        min-height: 50px;
        margin: 0 1em;
        padding: 0 0;
        border-bottom: 0 solid rgba(0,0,0,0);

        .header-title {
            @include reset-margins;
            position: relative;
            max-width: calc(100% - 50px);
            padding: 1.2em 0 0 1em;
            font-size: 1.6rem;
            font-weight: 700;
            line-height: 1.2;
            letter-spacing: -.06rem;
            text-transform: uppercase;
            color: $black;

            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 4px;
                height: 40px;
                background-color: $black;
            }
        }

        .bookmark-button {
            @include reset-button;
            position: absolute;
            top: 10px;
            right: 0;
            width: 38px;
            height: 38px;
            margin: 0 0;
            padding: .2em;
            color: $dark-black;
            border-radius: 50%;
            z-index: 3;

            &:after {
                position: relative;
                top: 0;
                left: 0;
                content: '\e867';
                font-family: 'Material Icons';
                font-size: 24px;
                font-weight: normal;
                line-height: 1.2;
            }

            &.bookmark-on {
                &:after {
                    content: '\e866';
                }
            }

            &:hover, &:focus, &:active {
                @include button-hover;
            }
        }
    }

    .card-info {
        margin: 0 1em;
        padding: 0 0;

        .info-date {
            font-size: 84%;
            font-weight: 400;
            color: $soft-black;
        }

        .info-title {
            font-size: 90%;
            font-weight: 600;
            letter-spacing: 0;
            text-transform: uppercase;
            color: $medium-black;
        }
    }

    .card-body {
        @include reset-flex;
        align-items: stretch;
        justify-content: flex-start;

        margin: 1em 1em;
        padding: 0 0;

        h3 {
            margin-bottom: .5em;
            font-weight: 700;
        }

        p {
            margin-bottom: 1em;
            font-size: 90%;
            line-height: 1.2;
            color: $medium-black;
        }

        .map-pin {
            position: relative;
            margin-top: 1em;
            padding-left: 1.3em;
            font-weight: 600;
            text-transform: uppercase;
            color: $soft-black;

            &:after {
                @include icons-ionicons(-2px,auto,auto,0);
                content: '\f3a3';
            }
        }
    }

    .card-footer {
        margin: 0 0 auto;
        padding: 0 0;
        background-color: rgba(0,0,0,0);
        border-top: none;

        .view-more {
            min-width: auto;
            padding: 1em 3.6em 1em 1.2em;
            font-size: 90%;
            line-height: 1;
            white-space: nowrap;
            z-index: 3;

            &:after {
                right: 26px;
                top: 8px;
                font-size: 36px;
            }

            &:hover, &:focus, &:active {
                padding-right: 21em;
            }
        }
    }

    .button-wrapper {
        .view-more {
            margin: 6rem 0 3rem;
        }
    }

