        }
    }

    .swiper-button-prev,
    .swiper-button-next {
        width: 60px;
        height: 60px;
        color: $black;
        border-radius: 50%;
        outline: none;
        transition: .2s;

        &:hover,&:focus {
            transform: scale(1.2);
        }

        &:active {
            color: inherit;
            background-color: $light-black;
        }
    }

    .swiper-button-prev {
        left: -60px;
    }

    .swiper-button-next {
        right: -60px;
    }

    .swiper-container-horizontal>.swiper-pagination-progressbar {
        width: 100%;
        height: 1px;
        left: 0;
        top: auto;
        bottom: 4px;
    }

    .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
        top: -2px;
        height: 6px;
        background: $black;
    }

    .section-header {
        margin-bottom: .6em;
    }

    img {
        width: 100%;
        height: auto;
        margin-bottom: 1em;
    }

}


