.event-speakers {
    margin-bottom: 16em;

    .speakers-wrapper {
        @include reset-flex-horizontal;
        justify-content: space-between;
        align-items: stretch;
        align-content: space-between;
        margin: 0 -.8em;
        height: 200px;

        .speakers-item {
            flex: 1;
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: flex-start;
            align-content: flex-start;
            margin: 0 .8em;
            padding: 1em;
            background-color: $light-black;
            @include smaller-text;
            @include box-hover-outline;

            > div {
                margin-bottom: .3em;
            }

            .photo {
                @include reset-figure;
                position: absolute;
                bottom: -40px;
                width: 80px;
                height: 80px;
                margin-top: auto; // flex align to bottom
                border-radius: 50%;
                transition: all .2s ease-out;
                z-index: 1;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 50%;
                    border: 4px solid $white;
                    transition: all .2s ease-out;
                }
            }

            &:hover,&:focus,&:active {
                background-color: $white;

                .photo {
                    border-radius: 6px;

                    img {
                        border-radius: 6px;
                    }
                }
            }

            .name,
            .company,
            .cv-link {
                text-transform: uppercase;
            }

            .company,
            .position {
                color: $medium-black;
            }

            .name {
                margin-bottom: .6em;
                font-weight: 700;
            }

            .company {
                font-weight: 600;
            }

            .cv-link {
                position: relative;
                // display: inline-block;
                margin-top: .6em;
                padding-right: 1em;
                font-size: 1.4rem;
                font-weight: 700;
                line-height: 1;
                color: $black;

                &:after {
                    @include icons-ionicons(-2px,0,auto,auto);
                    content: '\f3d3';
                    font-size: 18px;
                }

                &:hover,&:focus,&:active {
                    @include link-hover;
                }
            }
        }
    }
}




//
// SERVICES
// --------------------------------------------------------------------------

.insights-grid {
    margin: 6em auto 8em;
    // border: 1px solid red;
}

.grid-wrapper {
    display: -ms-grid;
    display: grid;
    grid-gap: 10px;

    // grid-gap IE11
    padding: 10px 0 0 10px;

    $col-width: (100% / 3);
    $row-height: 140px;

    -ms-grid-columns: $col-width $col-width $col-width;
    -ms-grid-rows: $row-height $row-height $row-height $row-height;

    grid-template-columns: $col-width $col-width $col-width;
    grid-template-rows: $row-height $row-height $row-height $row-height;

    justify-content: center;
    align-content: center;

    color: #444;
    background-color: #fff;

    .box {
        font-size: 150%;
        padding: 20px;
        color: #333;
        background-color: #fcf;

        // grid-gap IE11
        border: 0 solid $white;
        border-width: 0 10px 10px 0;

        &.a {
            -ms-grid-column: 1;
            -ms-grid-column-span: 2;
            -ms-grid-row: 1;
            -ms-grid-row-span: 2;

            grid-column: 1 / span 2;
            grid-row: 1 / span 2;
        }
        &.b {
            -ms-grid-column: 3;
            -ms-grid-row: 1;
            -ms-grid-row-span: 2;

            grid-column: 3;
            grid-row: 1 / span 2;
        }
        &.c {
            -ms-grid-column: 1;
            -ms-grid-row: 3;
            -ms-grid-row-span: 2;

            grid-column: 1;
            grid-row: 3 / span 2;
        }
        &.d {
            -ms-grid-column: 2;
            -ms-grid-row: 3;

            grid-column: 2;
            grid-row: 3;
        }
        &.e {
            -ms-grid-column: 3;
            -ms-grid-row: 3;

            grid-column: 3;
            grid-row: 3;
        }
        &.f {
            -ms-grid-column: 2;
            -ms-grid-row: 4;

            grid-column: 2;
            grid-row: 4;
        }
        &.g {
            -ms-grid-column: 3;
            -ms-grid-row: 4;

            grid-column: 3;
            grid-row: 4;
        }
    }
}
