.search-page {
    margin-bottom: 8rem;

    .search-filters {
        margin: 6rem auto 4rem;
        // border: 1px solid blue;

        .find-lawyer {
            margin-bottom: 4em;
        }

        .filter-lawyer {
            .row {
                padding: 0 2em;
            }
        }

